<template>
  <div>
    <p>
      <a
        href="https://namu.wiki/w/%EB%A3%AC%EC%96%B4%20%EC%95%84%EC%9D%B4%ED%85%9C"
        target="_blank"
        >나무위키의 데이터</a
      >를 참고하여 작성되었습니다.
    </p>
    <RuneButton
      class="btn btn-outline-dark m-1"
      v-for="rune in runeList"
      :key="rune.id"
      :id="rune.id"
      :class="isSelectedRune(rune.id) ? 'active' : ''"
      @click="toggleRune(rune.id)"
    >
    </RuneButton>
    <RunewordList :selectedRuneIdList="selectedRuneIdList" />
  </div>
</template>

<script>
import runeList from "@/assets/diablo2/rune.json";

import RunewordList from "@/components/diablo2/RunewordList.vue";
import RuneButton from "@/components/diablo2/RuneButton.vue";

export default {
  components: { RunewordList, RuneButton },
  data() {
    return {
      runeList,
      selectedRuneIdList: [],
    };
  },
  methods: {
    toggleRune(runeId) {
      if (this.isSelectedRune(runeId)) {
        this.selectedRuneIdList.splice(
          this.selectedRuneIdList.indexOf(runeId),
          1
        );
      } else {
        this.selectedRuneIdList.push(runeId);
      }
    },
    isSelectedRune(runeId) {
      var isSelected = false;
      for (var i = 0; i < this.selectedRuneIdList.length; i++) {
        if (this.selectedRuneIdList[i] == runeId) {
          isSelected = true;
          break;
        }
      }
      return isSelected;
    },
  },
};
</script>

<style></style>
